<table class="table table-bordered"><thead><tr><th class="text-center align-middle" rowspan="2">Action</th><th class="text-center align-middle" rowspan="2">Time</th><th class="text-center align-middle" rowspan="2">Price</th><th class="text-center align-middle" rowspan="2">Strength</th><th class="text-center align-middle" colspan="3">Divergence 1</th><th class="text-center align-middle" colspan="3">Divergence 2</th></tr><tr><th class="text-center">Time</th><th class="text-center">Price</th><th class="text-center">RSI</th><th class="text-center">Time</th><th class="text-center">Price</th><th class="text-center">RSI</th></tr></thead><tbody><tr v-for="swingTrade in swingTrades"><td class="text-center">{{ swingTrade.buy ? 'Buy' : 'Sell'}}</td><td class="text-center">{{ swingTrade.time }}</td><td class="text-end">{{ swingTrade.price?.toFixed(2) }}</td><td class="text-end">{{ swingTrade.strength.toFixed(2) }}</td><td class="text-center">{{ swingTrade.divergence_1.time }}</td><td class="text-end">{{ swingTrade.divergence_1.price.toFixed(2) }}</td><td class="text-end">{{ swingTrade.divergence_1.rsi.toFixed(2) }}</td><td class="text-center">{{ swingTrade.divergence_2.time }}</td><td class="text-end">{{ swingTrade.divergence_2.price.toFixed(2) }}</td><td class="text-end">{{ swingTrade.divergence_2.rsi.toFixed(2) }}</td></tr></tbody></table>